@import url(https://fonts.googleapis.com/css2?family=Gideon+Roman&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --charleston-green: #253031ff;
    --dark-slate-gray: #315659ff;
    --celadon-blue: #2978a0ff;
    --ecru: #bcab79ff;
    --beau-blue: #c6e0ffff;
    --blue-sapphire: #005f73ff;
    --viridian-green: #0a9396ff;
    --peach-orange: #ff914d;
    --middle-blue-green: #94d2bdff;
    --medium-champagne: #e9d8a6ff;
    --gamboge: #ee9b00ff;
    --alloy-orange: #ca6702ff;
    --mahogany: #bb3e03ff;
    --rufous: #ae2012ff;
    --ruby-red: #9b2226ff;
    --content-background-color: rgb(189, 193, 140);
}

* {
    box-sizing: border-box;
    scroll-behavior: smooth;
}

h1 {
    font-family: 'Gideon Roman', monospace;
    position: absolute;
    z-index: 2;
}

body::-webkit-scrollbar {
    display: none;
}

body {
    /* background-image: url('../images/pexels-bruno-scramgnon-585759.jpg'); */
    /* background-position: top; */
    background-size: cover;
    background: linear-gradient(#ee9b00ff, #ff914d, #c6e0ffff, #2978a0ff);
    background: linear-gradient(var(--gamboge), var(--peach-orange), var(--beau-blue), var(--celadon-blue));
}


.shape {
    position: absolute;
    overflow: hidden;
    height: 480px;
    width: 100%;
}

.shape::after {
    border-radius: 100%;
    position: absolute;
    right: -200px;
    left: -200px;
    top: -200px;
    content: '';
    bottom: 10px;
    background: radial-gradient(#ee9b00ff, #ca6702ff, #ae2012ff, #9b2226ff);
    background: radial-gradient(var(--gamboge), var(--alloy-orange), var(--rufous), var(--ruby-red));
}

.main-circle {
    width: 300px;
    height: 300px;
    border: 3px ridge #9b2226ff;
    border: 3px ridge var(--ruby-red);
    background-image: url(/tevissaur/bio/static/media/lion.0f3c391f.svg);
    background-position: center;
    background-size: cover;
    box-shadow: 0px 0px 100px #ee9b00ff, 50px 50px 150px #ff914d, -50px 50px 150px #ff914d;
    box-shadow: 0px 0px 100px var(--gamboge), 50px 50px 150px var(--peach-orange), -50px 50px 150px var(--peach-orange);
    z-index: 1;
    transition: all 0.5s;
}

.circle {
    width: 80px;
    height: 80px;
    border: 2px solid black;
    /* text-shadow: 0 0 5px rgba(85, 85, 85, 0.445); */
    /* background-image: url('../images/link-image.svg');
    background-position: center;
    background-size: cover; */
    background-color: #ee9b00ff;
    background-color: var(--gamboge);
    text-decoration: none;
    color: #253031ff;
    color: var(--charleston-green);
    vertical-align: middle;
    font-family: 'Gideon Roman', cursive;
    font-weight: bold;
    font-size: 1.2rem;
    z-index: 1;
    transition: all 0.3s ease-out;
}

#link-container {
    height: 100px;
    grid-gap: 15px;
    gap: 15px;
}

#nav-circle-1 {
    align-self: flex-start;
}

#nav-circle-2 {
    align-self: flex-end;
}

#nav-circle-3 {
    align-self: flex-start;
}

.circle:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    z-index: 999;
    color: black;
}

.main-circle:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.link-text {
    font-size: 2.75rem;
    transition: all 0.5s ease-out;
}

.hidden {
    display: none;
    opacity: 0;
    font-size: 0;
    transition: all 0.3s;
}

#socials {
    max-width: -webkit-min-content;
    max-width: min-content;
    max-height: -webkit-min-content;
    max-height: min-content;
    z-index: 9999;
}


#socials>a, #socials>button {
    width: 60px;
    height: 60px;
    border: 2px solid black;
    color: rgb(189, 193, 140);
    color: var(--content-background-color);
    font-size: 2.5rem;
    text-align: center;
    background-color: #9b2226ff;
    background-color: var(--ruby-red);
    display: flex;
    justify-content: center;
}

#socials-git:hover, #socials-pdf:hover, #socials-linked:hover, #socials-reveal:hover {
    color: #e9d8a6ff;
    color: var(--medium-champagne);
}

#bio-title, #work-title, #contact-title, #skills-title {
    position: relative;
    border: 2px solid black;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    margin: 0;
    font-size: 3.5rem;
}

#email {
    position: relative;
    border: 2px solid black;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    margin: 0;
    font-size: 1.8rem;
}

#email::after {
    content: '';
    background: black;
    height: 50px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
}

#bio-title::after {
    content: '';
    background: black;
    height: 50px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
}

#skills-title::after {
    content: '';
    background: black;
    height: 50px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
}

#work-title::after {
    content: '';
    background: black;
    height: 90px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
}

#contact-title::after {
    content: '';
    background: black;
    height: 51px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
}

#bio-content::before {
    content: '';
    background: black;
    height: 58px;
    left: 50%;
    position: absolute;
    bottom: 100%;
    z-index: -1;
    width: 1px;
}

#bio-content {
    border: 2px solid black;
    text-align: center;
    margin: 38px auto 0 auto;
    font-size: 2rem;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    position: relative;
}

#bio-pic {
    width: 600px;
    height: 600px;
    border: 2px solid black;
    background-image: url(/tevissaur/bio/static/media/20210523_142916.a78b6bec.jpg);
    background-size: cover;
    background-position: center;
}


#contact-form-container {
    border: 2px solid black;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    text-align: center;
    margin: 38px auto 0 auto;
    font-size: 2rem;
}

.work-container {
    position: relative;
}

.solo-work-image {
    text-decoration: none;
    border: 2px solid black;
    width: 350px!important;
    height: 350px!important;
    overflow: hidden;
    vertical-align: middle;
    z-index: 999;
    margin: 2rem auto 5% auto!important;
}

.solo-work-desc {
    border: 2px solid black;
    text-align: center;
    margin: 38px auto 0 auto;
    min-height: 200px;
    font-size: 2rem;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    position: relative;
}

.solo-work-skills {
    border: 2px solid black;
    text-align: center;
    margin: 38px auto 0 auto;
    min-height: 200px;
    font-size: 2rem;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    position: relative;
}

.solo-work-author {
    border: 2px solid black;
    text-align: center;
    margin: 38px auto 0 auto;
    font-size: 2rem;
    background-color: rgb(189, 193, 140);
    background-color: var(--content-background-color);
    position: relative;
}

.solo-work-skills::after {
    content: '';
    background: black;
    height: 40px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
    transition: all 1s;
}
.solo-work-author::after {
    content: '';
    background: black;
    height: 40px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
    transition: all 1s;
}
.solo-work-desc::after {
    content: '';
    background: black;
    height: 40px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
    transition: all 1s;
}

.work-content, .work-content-last {
    text-decoration: none;
    border: 2px solid black;
    position: relative;
    width: 300px;
    height: 300px;
    margin: 2rem auto 0 auto;
    overflow: hidden;
    transition: all 0.5s;
    vertical-align: middle;
}
.work-content-last {
    margin-bottom: 5%;
}

.work-content:hover, .work-content-last:hover {
    z-index: 999;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.work-content div, .work-content-last div {
    width: 100%;
    height: 100%;
    opacity: 0;
    font-size: 2rem;
    font-weight: bold;
    text-decoration: none;
    padding: 30% 20%;
    color: black;
    transition: all 0.5s;
}

.work-content:hover div, .work-content-last:hover div {
    font-size: 2rem;
    opacity: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(155, 34, 38, 0.616);
}

.work-content::after {
    content: '';
    background: black;
    height: 40px;
    left: 50%;
    position: absolute;
    top: 101%;
    width: 1px;
    transition: all 1s;
}

.work-content:hover .work-content::after {
    opacity: 0;
    width: 0;
}

.work-content::after:hover {
    opacity: 0;
}



#work-net-grill {
    background-image: url(/tevissaur/bio/static/media/Logo.e52a3a2b.png);
    background-position: center;
    background-size: cover;
    background-color: black;
}

#work-forene {
    background-image: url(/tevissaur/bio/static/media/Forene.f555fb84.jpg);
    background-position: center;
    background-size: cover;
}

#work-ttt {
    background-image: url(data:image/png;base64,);
