    background-size: cover;
}

#work-chicken {
    background-image: url(/tevissaur/bio/static/media/garden-square1.a787a628.jpg);
    background-position: top left;
    background-size: cover;
}

.work-row-two::before {
    content: '';
    background: black;
    height: 100px;
    left: 50%;
    position: absolute;
    top: -48px;
    width: 1px;
    transition: all 1s;
}

#nav-circle-git, #nav-circle-linked {
    width: 150px;
    height: 150px;
    color: #253031ff;
    color: var(--charleston-green);
    font-size: 5rem;
    text-align: center;
    padding: 1rem;
    background-color: #ee9b00ff;
    background-color: var(--gamboge);
    box-shadow: 0px 0px 100px #ee9b00ff;
    box-shadow: 0px 0px 100px var(--gamboge);
}

#nav-circle-git>i, #nav-circle-linked>i {
    margin: auto auto;
}

@media screen and (min-width: 769px) {
    .work-content::after {
        display: none;
    }

    #work-shape {
        width: 70%;
        height: 1px;
        left: 15%;
        top: 54%;
        background: black;
    }
}



@media screen and (max-width: 768px) {
    .work-content::after {
        display: none;
    }

    #work-shape {
        width: 1px;
        height: 100%;
        left: 50%;
        top: 15%;
        padding: 0;
        background: black;
    }

    #bio-title, #work-title, #contact-title, #skills-title {
        font-size: 2.5rem;
    }

    #email {
        font-size: 1.25rem;
    }

    #bio-pic {
        position: relative;
        width: 350px;
        height: 350px;
    }

    #bio-content {
        font-size: 1.5rem;
    }

    #link-container {
        grid-gap: 0;
        gap: 0;
    }

    #nav-circle-git, #nav-circle-linked {
        display: none;
    }
}

@media screen and (min-width: 1440px) {
    .work-content, .work-content-last {
        width: 300px;
        height: 300px;
    }
}

@media screen and (min-width: 2000px) {
    .main-circle {
